<template>
  <div class="head-bar">
    <div class="header-ico">
      <i class="el-icon-s-home" @click="go_home()" style="cursor: pointer"></i>
    </div>
    <div class="logo">信息管理系统</div>
    <div class="head-right">
      <div class="head-user-con">
        <div @click="handleFullScreen" class="btn-fullscreen">
          <el-tooltip
            :content="fullscreen ? `退出全屏` : `全屏`"
            effect="dark"
            placement="bottom"
          >
            <i class="el-icon-rank"></i>
          </el-tooltip>
        </div>

        <div class="user-avatar">
<!--不同身份头像不同-->
              <img src="../assets/student1.jpg" v-if="$store.state.status.userType===1" style="width: 55px; height: 55px"/>
              <img src="../assets/teacher.jpg" v-if="$store.state.status.userType===2" style="width: 55px; height: 55px"/>
              <img src="../assets/admin.jpg" v-if="$store.state.status.userType===3" style="width: 55px; height: 55px"/>
        </div>

        <el-dropdown @command="handleCommand" class="user-name" trigger="click">
          <span class="el-dropdown-link" style="font-size: larger">
            {{ swnumber }}
            <i class="el-icon-caret-bottom"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="logout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>
<script>
import { logout } from "../api/login";
// 导出一个vue对象
export default {
  name: "Header",
  data() {
    return {
      fullscreen: false
    };
  },
  computed: {
    swnumber() {
      return this.$store.state.status.swnumber;
    }
  },
  methods: {
    handleCommand(command) {
      if (command === "logout") {
        logout().then(() => {
          this.$store.commit("logout");
          this.$message.success("注销成功");
          this.$router.push("/login");
        });
      }
    },
    handleFullScreen() {
      let element = document.documentElement;
      if (this.fullscreen) {
        if (document.exitFullscreen) {
          document.exitFullscreen();
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.msExitFullscreen) {
          document.msExitFullscreen();
        }
      } else {
        if (element.requestFullscreen) {
          element.requestFullscreen();
        } else if (element.webkitRequestFullScreen) {
          element.webkitRequestFullScreen();
        } else if (element.mozRequestFullScreen) {
          element.mozRequestFullScreen();
        } else if (element.msRequestFullscreen) {
          // IE11
          element.msRequestFullscreen();
        }
      }
      this.fullscreen = !this.fullscreen;
    },
    go_home(){
      var routePath=this.$route.path;
      if (routePath.indexOf("teacher") != -1){
        this.$router.push({ name: "teacher-home" })
      }
      else if (routePath.indexOf("admin") != -1){
        this.$router.push({ name: "admin-home" })
      }
      else {
        this.$router.push({ name: "student-home" })
      }
      // $router.push({ name: "login" })
    },
  }
};
</script>
